import { BrowserModule } from '@angular/platform-browser';
import { ErrorHandler, NgModule } from '@angular/core';
import { IonicApp, IonicErrorHandler, IonicModule } from 'ionic-angular';
import { SplashScreen } from '@ionic-native/splash-screen';
import { StatusBar } from '@ionic-native/status-bar';
import { HttpModule } from '@angular/http';

import { MyApp } from './app.component';
import { HomePage } from '../pages/home/home';
import { LandingPage } from '../pages/landing/landing';
import { NewCardPage } from '../pages/new-card/new-card';
import { ReceiptsPage } from '../pages/receipts/receipts';
import { ResourcesProvider } from '../providers/resources/resources';
import { YearPopoverPage } from '../pages/year-popover/year-popover';

@NgModule({
  declarations: [
    MyApp,
    HomePage,
    LandingPage,
    NewCardPage,
    ReceiptsPage,
    YearPopoverPage
  ],
  imports: [
    BrowserModule,
    HttpModule,
    IonicModule.forRoot(MyApp, {}, 
{
  links: [
    { loadChildren: '../pages/year-popover/year-popover.module#YearPopoverPageModule', name: 'YearPopoverPage', segment: 'year-popover', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/receipts/receipts.module#ReceiptsPageModule', name: 'ReceiptsPage', segment: 'receipts', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/new-card/new-card.module#NewCardPageModule', name: 'NewCardPage', segment: 'new-card', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/landing/landing.module#LandingPageModule', name: 'LandingPage', segment: 'landing', priority: 'low', defaultHistory: [] }
  ]
})
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,
    HomePage,
    LandingPage,
    NewCardPage,
    ReceiptsPage,
    YearPopoverPage
  ],
  providers: [
    StatusBar,
    SplashScreen,
    {provide: ErrorHandler, useClass: IonicErrorHandler},
    ResourcesProvider
  ]
})
export class AppModule {}



